import { Component, OnInit, ANALYZE_FOR_ENTRY_COMPONENTS } from '@angular/core';
import { TestService } from 'src/app/services/Test.service';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Test } from 'src/shared/Test';

@Component({
  selector: 'app-Test',
  templateUrl: './Test.component.html',
  styleUrls: []
})
export class TestComponent implements OnInit {

  constructor(public service: TestService, private toastr: ToastrService) { }

  ngOnInit() {
    this.resetForm();
    this.service.refreshList();
  }

  resetForm(form?: NgForm){
    if (this.service.formData.Id != null){
      form.resetForm();
    }
    this.service.formData = {
        Id: 0,
        Name: ''
      };
  }

  onSubmit(form: NgForm){
    if (this.service.formData.Id === 0){
      this.insertRecord(form);
    }
    else{
      this.updateForm(form);
    }
  }
  insertRecord(form: NgForm){
    this.service.postTest().subscribe(
      res => {
        this.resetForm(form);
        this.toastr.success('Test added', 'Test head');
        this.service.refreshList();
      },
      err => {
        console.log(err);
      }
    );
  }
  updateForm(form: NgForm){
    this.service.putTest().subscribe(
      res => {
        this.resetForm(form);
        this.toastr.success('Test update', 'Test head');
        this.service.refreshList();
      },
      err => {
        console.log(err);
      }
    );
  }
  onDelete(id){
    if (confirm('Are you sure to delete this record?')){
      this.service.deleteTest(id)
      .subscribe(res => {
        this.service.refreshList();
        this.toastr.warning('Deleted item', 'Header');
      });
    }
  }
  populateForm(item: Test){
this.service.formData = Object.assign({}, item);
  }
}
