import { Component, OnInit } from '@angular/core';
import { NewsService } from '../services/news.service';
import { AlertifyService } from '../services/alertify.service';
import { NgForm } from '@angular/forms';
import { CategoryService } from '../services/category.service';
import { Category } from '../models/category';

@Component({
  selector: 'app-news-detail',
  templateUrl: './news-detail.component.html',
  styleUrls: ['./news-detail.component.css']
})
export class NewsDetailComponent implements OnInit {
listofCategories: any[];
selectedValue: any;

  constructor(public newsService: NewsService,
    private categoryService: CategoryService,
     private alertify: AlertifyService) { }

  ngOnInit() {
    this.resetForm();
    this.getCategories();
  }
  SubmitNews(form: NgForm) {
    console.log(this.selectedValue);
    this.newsService.newsData.CategoryId = +this.selectedValue;
    if (this.newsService.newsData.Id === 0) {
      this.insertRecord(form);
    } else {
      this.updateRecord(form);
    }
  }

  insertRecord(form: NgForm) {
    this.newsService.postNews().subscribe(
      (res) => {
        this.resetForm(form);
        this.alertify.success(
          'Submitted successfully'
        );
        this.newsService.refreshList();
      },
      (err) => {
        this.alertify.error(err);
      }
    );
  }
  updateRecord(form: NgForm) {
    this.newsService.putNews().subscribe(
      (res) => {
        this.resetForm(form);
        this.alertify.success(
          'Submitted successfully'
        );
        this.newsService.refreshList();
      },
      (err) => {
        this.alertify.error(err);
      }
    );
  }
  resetForm(form?: NgForm) {
    if (form != null) {
      form.resetForm();
    }
    this.newsService.newsData = {
      Id: 0,
      Name: '',
      Description: '',
      CategoryId: 0,
    };
  }
  getCategories(){
    this.categoryService.getAll().toPromise().then(res => this.listofCategories = res as Category[]);
  }
}
