import { Injectable } from '@angular/core';
import { Test } from '../../shared/Test';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class TestService {
  formData: Test = {} as Test;
  list: Test[];

  constructor(private http: HttpClient) {}

  postTest() {
    return this.http.post('http://localhost:61364/api/' + 'Test', this.formData);
  }
  putTest() {
    return this.http.put('http://localhost:61364/api/' + 'Test/' + this.formData.Id, this.formData);
  }
  deleteTest(id: number) {
    return this.http.delete('http://localhost:61364/api/' + 'Test/' + id);
  }
  refreshList(){
    return this.http.get('http://localhost:61364/api/' + 'Test')
    .toPromise()
    .then(res =>  this.list = res as Test[]);
  }
}
