import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { News } from '../models/news';

@Injectable({
  providedIn: 'root'
})
export class NewsService {
newsData: News = {} as News;
listOfNews: News[];

constructor(private http: HttpClient) { }

postNews() {
  return this.http.post('http://localhost:61364/api/' + 'News', this.newsData);
}
putNews() {
  return this.http.put('http://localhost:61364/api/' + 'News/' + this.newsData.Id, this.newsData);
}
deleteNews(id: number) {
  return this.http.delete('http://localhost:61364/api/' + 'News/' + id);
}
refreshList(searchQuery?: string){
  return this.http.get('http://localhost:61364/api/' + 'News/' + searchQuery)
  .toPromise()
  .then(res =>  this.listOfNews = res as News[]);
}
}
